.main-page{
    height: 100dvh;
    display: flex;
    flex-direction: column;
    background-color: var(--red);
    background-image: url(../../../public/assets/images/quiz-back.jpg);
    background-repeat: no-repeat;
    background-position: center 0;
    background-size: cover;
}

.main-page__counter{
    width: 85px;
    height: 43px;
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: rgba(255, 255, 255, 0.20);
    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.3px;
    color: var(--white);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    margin-top: 25px;
}

.main-content{
    height: calc(65dvh - 130px);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    padding: 0 14px 14px;
    box-sizing: border-box;
}

.main-content__title{
    text-align: center;
    margin-top: auto;
    margin-bottom: auto;
}

.main-content__buttons .button{
    border: 1px solid #E7E7E7;
    box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.05);
}

.main-content__buttons{
    display: flex;
    flex-direction: column;
    grid-gap: 13px;
    margin-top: 5px;
}

