
.loading-page{
    /* background-size: 100% 100%; */
    background-color: var(--red) !important;
    /* background-image: url(../../../public/assets/images/loading-bg-top.png), url(../../../public/assets/images/loading-bg-bottom.png); */
    background-repeat: no-repeat;
    background-position: center 0;
    background-size: cover;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.loading-page__company-logo{
    margin-left: auto;
    margin-right: auto;
    margin-top: 30dvh;
}

.loading-page__logo{
    margin-top: auto;
    margin-bottom: auto;
}

.loading-page__loading-block{
    margin-left: auto;
    margin-right: auto;
}

.loading-block{
   max-width: 320px;
   width: 100%;
   height: 6px;
   box-sizing: border-box;
   background-color: rgba(255, 255, 255, 0.30);
}

.container__loading-block{
    margin-top: auto;
    margin-bottom: 68px;
}

.loading-page__main-pic{
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
}

.loading-block__bar{
    height: 100%;
    background: var(--white);
    width: 0;
    animation: loading 2.5s;
    animation-fill-mode: forwards;
}

.loading-wrapper{
    box-sizing: border-box;
    /* background-image: url(../images/loading-pattern.png); */
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100% auto;
    transform: translateY(-50px);
}

@keyframes loading {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}
