.compet{
    background-color: var(--red);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 100dvh;
    padding: 28px 26px 36px;
    box-sizing: border-box;
    background-size: cover;
    background-position: center 0;
}

.compet__icon{
    margin-bottom: auto;
}

.compet__title{
    text-align: center;
    margin-bottom: 12px;
}

.compet__text{
    text-align: center;
    margin-bottom: 44px;
}