.button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
    cursor: pointer;
    text-align: center;
}

.button_size_small{
    height: 60px;
    border-radius: 10px;

    font-size: 16px;
    font-style: normal;
    font-weight: 600;
}

.button_size_big{
    height: 80px;
    border-radius: 20px;

    font-size: 18px;
    font-style: normal;
    font-weight: 600;
}

.button_color_red{
    background-color: var(--red);
    color: var(--white);
}

.button_color_white{
    background-color: var(--white);
    color: var(--red);
}

.button_color_transparent{
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: rgba(255, 255, 255, 0.20);
    color: var(--white);
}