.start{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    background-image: url(../../../public/assets/images/start-bg.jpg);
    background-repeat: no-repeat;
    background-position: center 0;
    background-size: cover;
    padding-left: 14px;
    padding-right: 14px;
}

.start__pic{
    margin: auto;
}

.start__title{
    text-align: center;
    margin-bottom: 14px;
}

.start__text{
    text-align: center;
    margin-bottom: 10dvh;
}

.start__buttons{
    width: 100%;
    margin-bottom: 16px;
    display: flex;
    flex-direction: column;
    grid-gap: 14px;
    margin-top: 20px;
}

