@font-face{
    font-family: "Myriad Pro";
    src: url("../fonts/MyriadPro-Bold/MyriadPro-Bold.eot");
    src: url("../fonts/MyriadPro-Bold/MyriadPro-Bold.eot?#iefix")format("embedded-opentype"),
        url("../fonts/MyriadPro-Bold/MyriadPro-Bold.woff")format("woff"),
        url("../fonts/MyriadPro-Bold/MyriadPro-Bold.woff2")format("woff2"),
        url("../fonts/MyriadPro-Bold/MyriadPro-Bold.ttf")format("truetype");
    font-weight: 700;
    font-style:normal;
    font-display:swap;
}

@font-face{
    font-family: "Myriad Pro";
    src: url("../fonts/MyriadPro-Semibold/MyriadPro-Semibold.eot");
    src: url("../fonts/MyriadPro-Semibold/MyriadPro-Semibold.eot?#iefix")format("embedded-opentype"),
        url("../fonts/MyriadPro-Semibold/MyriadPro-Semibold.woff")format("woff"),
        url("../fonts/MyriadPro-Semibold/MyriadPro-Semibold.woff2")format("woff2"),
        url("../fonts/MyriadPro-Semibold/MyriadPro-Semibold.ttf")format("truetype");
    font-weight:600;
    font-style:normal;
    font-display:swap;
}

@font-face{
    font-family: "Myriad Pro";
    src: url("../fonts/MyriadPro-Regular/MyriadPro-Regular.eot");
    src: url("../fonts/MyriadPro-Regular/MyriadPro-Regular.eot?#iefix")format("embedded-opentype"),
        url("../fonts/MyriadPro-Regular/MyriadPro-Regular.woff")format("woff"),
        url("../fonts/MyriadPro-Regular/MyriadPro-Regular.woff2")format("woff2"),
        url("../fonts/MyriadPro-Regular/MyriadPro-Regular.ttf")format("truetype");
    font-weight:400;
    font-style:normal;
    font-display:swap;
}

:root{
    --black: #000;
    --white: #FFFFFF;
    --red: #ED1C24;
    --grey: #F1F1F0
}

*{
    outline: none !important;
}

ul,
ol,
p,
div,
label,
h1,h2,h3{
    list-style: none;
    padding: 0;
    margin: 0;
}

button, button span{
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 0;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

img{
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

a,a:hover{
    text-decoration: none;
}

body,html{
    font-family: 'Myriad Pro', sans-serif;
    /* min-height: 100vh; */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.69);
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal{
    width: 100%;
    min-height: 45dvh;
    margin-left: 0;
    margin-right: 0;
    margin-top: auto;
    /* min-height: 420px; */
    background-color: #fff;
    border-radius: 20px 20px 0 0;
    box-sizing: border-box;
    padding: 55px 14px 20px;
    position: relative;
    display: flex;
    flex-direction: column;
}

.modal__pic{
    position: absolute;
    left: 0;
    right: 0;
    bottom: calc(100% - 45px);
    margin: auto;
}

.modal__close{
    display: flex;
    width: 70px;
    height: 70px;
    background-color: var(--red);
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    /* position: absolute; */
    right: 0;
    left: 0;
    margin: auto;
    top: 25px;
}

.loose-modal__buttons{
    margin-top: auto;
}

.container{
    min-height: 100vh;
    overflow: auto;
    position: relative;
    background-color: var(--white);
}

.loose-modal__title{
    text-align: center;
    margin-bottom: 5px;
}

.loose-modal__text{
    width: calc(100% - 70px);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 10px;
}

.app > div:not(.overlay){
    min-height: 590px;
    height: 100dvh;
}

@keyframes hide {
    0% {
        opacity: 100%;
    }

    50% {
        opacity: 100%;
    }

    100% {
        opacity: 0%;
    }
}
